@import url(https://fonts.googleapis.com/css2?family=Sora:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sora:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sora:wght@300;400;500;700&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

body{font-size:16px;font-family:Sora, sans-serif}.App{padding:40px}.App>*{margin:0 auto}

.number,.operator,.equal{-webkit-user-select:none;-ms-user-select:none;user-select:none;box-shadow:0 2px 2px rgba(0,0,0,0.15);border-radius:.4rem;padding:0.5rem 1rem;border:none;width:3rem;cursor:pointer;text-align:center;display:flex;justify-content:center;align-items:center}.number{background-color:#f1f3f4}.number:hover{background-color:#e3e7e9}.number:active{background-color:#c6ced2;box-shadow:0 0px 2px rgba(0,0,0,0.15)}.operator{font-weight:bold;background-color:#dadce0}.operator:hover{background-color:#cccfd4}.operator:active{background-color:#b0b5bd;box-shadow:0 0px 2px rgba(0,0,0,0.15)}.equal{color:white;background-color:#4285f4}.equal:hover{background-color:#2a75f3}.equal:active{background-color:#0d5bdd;box-shadow:0 0px 2px rgba(0,0,0,0.15)}

.screen{box-shadow:0 2px 2px rgba(0,0,0,0.15);border-radius:.3rem;border:none;min-height:20px;line-height:20px;text-align:right;padding:1.5rem 1rem;word-wrap:break-word;margin-bottom:10px;background:#0f2027;background:linear-gradient(to right, #eaf6fc, #b3e8ff, #cde3ec)}

.keypad{display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:0.5rem;gap:0.5rem}.operators{margin-left:1.5rem;display:grid;grid-template-columns:1fr 1fr;grid-gap:0.5rem;gap:0.5rem}.calculator{box-shadow:0 2px 6px rgba(0,0,0,0.13);border-radius:.4rem;background:#fdfdfd;width:450px;padding:1rem;display:grid;grid-template-columns:1fr 1fr}.calculator>div:first-child{grid-column:span 2}

